<template>
  <v-container>
    <div class="d-flex">
      <!-- back button -->
      <v-btn to="/" size="x-large" variant="plain">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <h1>History</h1>
    </div>
    <v-table fixed-header>
      <thead>
        <tr>
          <th class="text-left"></th>
          <th class="text-left">Date</th>
          <th class="text-left">Weight</th>
          <th class="text-left">Length</th>
          <th class="text-left">Height</th>
          <th class="text-left"></th>
          <!-- <th class="text-left">Calories</th> -->
        </tr>
      </thead>
      <tbody>
        <tr v-for="muestra in muestras" :key="muestra.id">
          <td>
            <!-- add small image on the muestra.image -->
            <v-img
              :src="`http://127.0.0.1:${this.url_port}/muestra_image/${muestra.image}`"
              width="150"
              height="150"
              contain
            ></v-img>
          </td>
          <td>{{ muestra.date }}</td>
          <td>{{ muestra.weight }}</td>
          <td>{{ muestra.length }}</td>
          <td>{{ muestra.height }}</td>
          <td>
            <v-btn
              :to="`/muestra/${muestra.id}`"
              size="x-large"
              variant="plain"
            >
              <v-icon>mdi-arrow-right</v-icon>
            </v-btn>
          </td>
          <!-- <td>{{ item.calories }}</td> -->
        </tr>
      </tbody>
    </v-table>
    <v-pagination v-model="page" :length="4" rounded="circle"></v-pagination>
  </v-container>
</template>
<script>
import axios from "axios";
import config from "../config";

export default {
  name: "History",
  data: () => ({
    muestras: [],
    page: 1,
  }),
  computed: {
    url_server() {
      return config.url_server();
    },
    url_port() {
      return config.url_port();
    },
  },
  methods: {
    fetchData() {
      const { url_server, page } = this;
      console.warn(`http://127.0.0.1:${this.url_port}/history/${page}`);
      axios
        .get(`http://127.0.0.1:${this.url_port}/history/${page}`)
        .then((response) => {
          console.warn(response.data.data);
          this.muestras = response.data.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    this.fetchData();
  },
  watch: {
    page() {
      this.fetchData();
    },
  },
};
</script>
<style scoped lang="scss"></style>
