<template>
  <v-container>
    <div class="d-flex">
      <!-- back button -->
      <v-btn to="/history" size="x-large" variant="plain">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <h1>Details</h1>
    </div>
    <v-row v-if="muestra">
      <v-col class="" cols="6">
        <img
          class="the_fuckin_image"
          :src="`http://127.0.0.1:${this.url_port}/muestra_image/${muestra.image}`"
          crossorigin="anonymous"
        />
        <!-- ref="capturedImage" -->
      </v-col>
    </v-row>
    <v-row v-if="muestra">
      <v-col cols="6" class="d-flex flex-column">
        <h2>Weight:</h2>
        <h1 class="ml-3">{{ muestra.weight }}G</h1>
        <!-- <h3 class="mb-2">Indicators</h3> -->
        <v-col cols="12" class="d-flex">
          <v-row>
            <v-col
              cols="2"
              v-for="(indicator, i) in indicators"
              class="d-flex flex-column align-center px-3 indicator-wrapper"
            >
              <span :class="`text-${indicator ? 'black' : 'grey'}`">
                Indicator {{ i + 1 }}
              </span>
              <div
                class="indicator"
                :class="indicator ? 'active' : 'inactive'"
              ></div>
            </v-col>
          </v-row>
        </v-col>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";
import config from "../config";

export default {
  name: "Muestra",
  data: () => ({
    muestra: null,
    indicators: [],
  }),
  computed: {
    url_server() {
      return config.url_server();
    },
    url_port() {
      return config.url_port();
    }
  },
  methods: {
    fetchData() {
      const { url_port, page } = this;
      axios
        .get(`http://127.0.0.1:${url_port}/select/${this.$route.params.id}`)
        .then((response) => {
          console.warn(response.data.data);
          this.muestra = response.data.data[0];
          const defects = JSON.parse(`[${this.muestra.defects}]`);
          defects.forEach(defect => {
            this.indicators[defect] = true;
          });
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  mounted() {
    this.fetchData();

    var {noOfDefects} = config;
    this.indicators = [...new Array(noOfDefects).fill(false)];
  },
  watch: {
    muestra: function (val) {
      console.warn(val);
      // this.indicators = val.indicators;
    },
    '$route.params.id': function (val) {
      console.warn(val);
      this.fetchData();
    }
  },
};
</script>

<style scoped lang="scss">

.inactive {
  background-color: red;
}
.active {
  background-color: greenyellow;
}

  .indicator {
  width: 24px;
  height: 24px;
  border-radius: 12px;
}


</style>
